import React, { Component } from 'react';
import './Movies.scss';
import {Link} from 'react-router-dom';



function Movies ({year, title, genres, summary, poster}) {
    return (
        <Link to={{
            pathname: '/detail',
            state: {year, title, genres, poster, summary}
        }}>
            <div className="movies">
                <img src={poster} alt={title} />
                <div className="movies_data">
                    <h3 className="movies_title">{title}</h3>
                    <h5 className="movies_year">{year}</h5>
                    <ul>
                        {
                            genres.map((data, index) => (<li key={index}>{data}</li>))
                        }
                    </ul>
                    <p className="movies_summary">{summary.slice(0, 50)}...</p>
                </div>
            </div>
        </Link>
    );
}

export default Movies;