import React, { Component } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import './Detail.scss';

class Detail extends Component {
    render() {
        const { location } = this.props
        return (
            <div className="wrap">
                <Header />
                <div className="detail-box">
                    <div className="detail-img"><img src={location.state.poster} alt={location.state.title} /></div>
                    <div className="detail-txt">
                        <p className="detail-title">{location.state.title}</p>
                        <p className="detail-year">{location.state.year}</p>
                        <p className="detail-genres">{location.state.genres.map((data)=><p>{data}</p>)}</p>
                        <p className="detail-summary">{location.state.summary}</p>
                    </div>
                </div>
                <Footer />
            </div>
        );
    }
}

export default Detail;